<template>
    <div class="user-mobile">
        <router-view />
        <van-tabbar
                v-model="active"
                active-color="#1989fa"
                inactive-color="#000"
                :route="true"
        >
            <van-tabbar-item to="/user/home" name="/user/home" icon="home-o">首页</van-tabbar-item>
            <van-tabbar-item to="/user/myWorkBook" name="/user/myWorkBook" icon="newspaper-o">记录</van-tabbar-item>
            <van-tabbar-item to="/user/mine" name="/user/mine" icon="manager-o">我的</van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
    import config from "../../config";
    import {getUserInfo} from "../../api";
    import {Toast} from "vant";

    export default {
        name: "index",
        data(){
            return{
                active: '/user/home',
                isShow: true
            }
        },
        watch: {
            '$route.path': function (newVal) {
                this.active = newVal;
            }
        },
        mounted() {
            // eslint-disable-next-line no-console
            console.log(this.$route.path);
            if(config.getCache('userId')){//axios canel
                getUserInfo({
                    userId: config.getCache('userId')
                }).then(data =>{
                    if(data.code === 200){
                        this.$store.dispatch('addUserInfo', {userInfo: data.result.userInfo});
                    }else{
                        Toast(data.data);
                        config.delCache('userId');
                        this.$store.dispatch('delUserInfo');
                        this.$router.replace('/user/login');
                    }
                })
            }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">

</style>
